.shadow {
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, .2),
    0px 8px 10px 1px rgba(0, 0, 0, .14),
    0px 3px 14px 2px rgba(0, 0, 0, .12);
}

.fadein{
    animation: fadein 0.3s ease-in;
    /* animation-delay: 0.5s; */
}
@keyframes fadein {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

.fadeout{
    animation: fadeout ease-out .3s;
}
@keyframes fadeout {
    0% { opacity: 1; }
    100% { opacity: 0; }
}

.fadeoutin{
    animation: fadeinout ease-out .3s;
}
@keyframes fadeinout {
    0% { opacity: 1; }
    50% { opacity: 0; }
    100% { opacity: 1; }
}