body {
  margin:0;
  touch-action: none; /* allow vertical scroll but prevent swipe to go back in msEdge */
  background-color: olivedrab;
  font-family: Roboto;
  overflow: hidden;
}

.container {
  position: relative; /* Must be relative to not cutoff draggable when outside container */
  height:100vh;
  width:calc(100vw - 3px);
  overflow-x:visible;
  touch-action: pan-y;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.list-item {
  width:calc(100% - 40px);
  height:50px;
  margin: 20px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: whitesmoke;
}

.drag-style {
  background-color: palegreen;
}
.shadow {
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, .2),
    0px 8px 10px 1px rgba(0, 0, 0, .14),
    0px 3px 14px 2px rgba(0, 0, 0, .12);
}

.fadein{
    -webkit-animation: fadein 0.3s ease-in;
            animation: fadein 0.3s ease-in;
    /* animation-delay: 0.5s; */
}
@-webkit-keyframes fadein {
    0% { opacity: 0; }
    100% { opacity: 1; }
}
@keyframes fadein {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

.fadeout{
    -webkit-animation: fadeout ease-out .3s;
            animation: fadeout ease-out .3s;
}
@-webkit-keyframes fadeout {
    0% { opacity: 1; }
    100% { opacity: 0; }
}
@keyframes fadeout {
    0% { opacity: 1; }
    100% { opacity: 0; }
}

.fadeoutin{
    -webkit-animation: fadeinout ease-out .3s;
            animation: fadeinout ease-out .3s;
}
@-webkit-keyframes fadeinout {
    0% { opacity: 1; }
    50% { opacity: 0; }
    100% { opacity: 1; }
}
@keyframes fadeinout {
    0% { opacity: 1; }
    50% { opacity: 0; }
    100% { opacity: 1; }
}
